import React, { useEffect, useRef, useState } from 'react'
import DataGrid, { Column, FilterRow, HeaderFilter, Pager, Paging, Scrolling, SearchPanel } from 'devextreme-react/data-grid'
import axios from 'axiosbase'
import getLoggedUserToken from 'components/utilities/getLoggedUserToken'
import { useHistory } from 'react-router-dom'
import './dataGrid.css'
import { loadMessages } from 'devextreme/localization'

loadMessages({
    "en": {
        "dxDataGrid-filterRowOperationContains": "",
        "dxDataGrid-filterRowOperationNotContains": "",
        "dxDataGrid-filterRowOperationStartsWith": "",
        "dxDataGrid-filterRowOperationEndsWith": "",
        "dxDataGrid-filterRowOperationEquals": "",
        "dxDataGrid-filterRowOperationNotEquals": "",
        "dxDataGrid-filterRowResetOperationText": "",
        "dxDataGrid-trueText": "",
        "dxDataGrid-falseText": "",
        "dxList-selectAll": " ",
        "dxDataGrid-headerFilterOK": "",
        "dxDataGrid-headerFilterCancel": "",
        "dxDiagram-dialogButtonOK": "",
        "dxDiagram-dialogButtonCancel": "",
        "dxDataGrid-headerFilterEmptyValue": "()",
        "dxDataGrid-filterRowShowAllText": "()",
        "dxCalendar-todayButtonText": "",
    }
})

export default function ProjectHandle() {
    const [data, setData] = useState([])

    useEffect(() => {
        axios.get('pps-infos/registered-companies', {
            headers: { 'Authorization': getLoggedUserToken() }
        }).then(res => {
            setData(res.data.data)
        })

        axios.get('users', {
            headers: { Authorization: getLoggedUserToken() },
            params: { role: 'bh_zovloh' }
        }).then(res => {
            setConsultants(res.data.data)
        })
    }, [])

    const containerRef = useRef()

    const [width, setWidth] = useState()

    useEffect(() => {
        const handleResize = () => {
            setWidth(containerRef.current?.clientWidth)
        }
        if (width === undefined) handleResize()
        window.addEventListener('resize', handleResize)

        return () => window.removeEventListener('resize', handleResize)
    }, [])

    const [consultants, setConsultants] = useState([])

    const getConsultantName = (id) => {
        if (id) {
            const consultant = consultants.filter(consultant => consultant.id === id)[0]
            return consultant?.firstname
                ? `${consultant.lastname?.substr(0, 1).toUpperCase()}. ${consultant.firstname}`
                : null
        } else {
            return null
        }
    }

    const calculateCellValueBdsUser = (rowdata) => {
        return getConsultantName(rowdata.project?.bds_userId)
    }

    return (
        <div className="tw-text-sm tw-text-gray-700 tw-pb-10 tw-w-full tw-overflow-hidden">
            <div className="tw-px-3 tw-pt-2 tw-pb-6 tw-shadow-md tw-bg-white tw-flex tw-flex-col tw-rounded tw-overflow-hidden tw-w-full" ref={containerRef}>
                <div className="tw-p-2 tw-mt-6 tw-text-lg tw-font-medium tw-text-center">
                     
                </div>

                <DataGrid
                    elementAttr={{ id: 'registered-companies-data-grid' }}
                    dataSource={data}
                    showBorders={true}
                    wordWrapEnabled={true}
                    rowAlternationEnabled={true}
                    columnAutoWidth={true}
                    width={width && width - 25}
                    showRowLines={true}
                    showColumnLines={true}
                    loadPanel={{ enabled: true, height: 300, text: ' ' }}
                    noDataText="  ."
                >
                    <SearchPanel visible={true} width={240} placeholder="..." />
                    <Scrolling mode="standard" columnRenderingMode="standard" showScrollbar="always" />
                    <Paging defaultPageSize={20} />
                    <Pager showPageSizeSelector={true} allowedPageSizes={[10, 20, 40]} showInfo={false} showNavigationButtons={true} />
                    <HeaderFilter visible={true} />
                    <FilterRow visible={true} />

                    <Column caption=" " cellRender={data => <ButtonNavProgress data={data} />} headerCellRender={HeaderCell} alignment="left" width={115} />
                    <Column dataField="companyname" caption=" " headerCellRender={HeaderCell} alignment="left" minWidth={120} />
                    <Column dataField="companyregister" caption="  " headerCellRender={HeaderCell} alignment="left" />
                    <Column dataField="criteria" caption="   " headerCellRender={HeaderCell} customizeText={customizeTextCriteria} cellRender={cellRenderCriteria} alignment="left" width={110} />
                    <Column dataField="esq" caption=" " headerCellRender={HeaderCell} customizeText={customizeTextEsq} cellRender={cellRenderEsq} alignment="left" width={102} />
                    <Column dataField="esm" caption="" headerCellRender={HeaderCell} alignment="center" width={110} />
                    <Column dataField="letterOfInterst" caption="   " headerCellRender={HeaderCell} calculateCellValue={calculateCellValueLetterOI} alignment="left" />

                    <Column caption=" " headerCellRender={HeaderCellMultiHeader}>
                        <Column dataField="project.status" caption="" cellRender={data => <ButtonNavStatus data={data} />} headerCellRender={HeaderCell} calculateCellValue={calculateCellValueStatus} alignment="left" width={140} />
                        <Column dataField="project.project_number" caption=" " headerCellRender={HeaderCell} alignment="left" width={119} />
                        <Column dataField="project.project_type_name" caption=" " headerCellRender={HeaderCell} alignment="left" />
                        <Column dataField="project.project_name" caption=" " headerCellRender={HeaderCell} alignment="left" minWidth={120} />
                        <Column dataField="project.project_duration" caption="  " headerCellRender={HeaderCell} customizeText={customizeTextDuration} alignment="left" />
                        <Column dataField="project.confirmed" caption=" " headerCellRender={HeaderCell} customizeText={customizeTextConfirmed} alignment="left" />
                        <Column dataField="project.bds_userId" caption=" " headerCellRender={HeaderCell} calculateCellValue={calculateCellValueBdsUser} minWidth={100} alignment="left" />
                    </Column>

                    <Column dataField="evidence" caption="  " headerCellRender={HeaderCell} calculateCellValue={calculateCellValueEvidence} alignment="left" />
                    <Column dataField="edpPlan" caption="  " headerCellRender={HeaderCell} calculateCellValue={calculateCellValueEdpPlan} alignment="left" />
                    <Column dataField="firstEvalution" caption="  " headerCellRender={HeaderCell} calculateCellValue={calculateCellValue5a} cellRender={cellRender5a} alignment="left" width={110} />
                    <Column dataField="evaluation5b" caption="  " headerCellRender={HeaderCell} calculateCellValue={calculateCellValueEvaluation5b} cellRender={cellRender5b} alignment="left" width={146} />
                    <Column dataField="lastEvalution" caption="  " headerCellRender={HeaderCell} calculateCellValue={calculateCellValue5c} cellRender={cellRender5c} alignment="left" width={131} />
                </DataGrid>
            </div>
        </div>
    )
}

const HeaderCell = (data) => (
    <div className="tw-text-center tw-font-medium tw-text-gray-700 tw-text-13px">
        {data.column.caption}
    </div>
)

const HeaderCellMultiHeader = (data) => (
    <div className="tw-font-medium tw-text-gray-70 tw-pl-4 tw-text-13px">
        {data.column.caption}
    </div>
)

const ButtonNavProgress = (data) => {
    const history = useHistory()
    const buttonClick = () => {
        const userId = data.data.data.userId
        const projectId = data.data.data.project?.id

        userId && history.push({
            pathname: '/progress',
            search: projectId ? `?userId=${userId}&projectId=${projectId}` : `?userId=${userId}`
        })
    }
    return <button className="tw-bg-gray-700 tw-rounded-sm tw-py-1 tw-px-2 tw-text-white tw-whitespace-nowrap focus:tw-outline-none active:tw-bg-gray-800 tw-transition-colors hover:tw-shadow-md" onClick={buttonClick}>
         
    </button>
}

export const statusNames = {
    editable: ' ',
    locked: ' ',
    approved: '',
    disapproved: '',
    cancelled: '',
}

const calculateCellValueStatus = (rowdata) => {
    return statusNames[rowdata.project?.status]
}

const ButtonNavStatus = (data) => {
    const projectId = data.data.data.project?.id
    const status = data.data.data.project?.status
    const history = useHistory()
    const buttonClick = () => {
        history.push({
            pathname: '/project-status',
            search: `?projectId=${projectId}`
        })
    }
    return status
        ? <button className="tw-bg-gray-700 tw-rounded-sm tw-py-1 tw-px-2 tw-text-white tw-whitespace-nowrap focus:tw-outline-none active:tw-bg-gray-800 tw-transition-colors hover:tw-shadow-md" style={{ width: 118 }} onClick={buttonClick}>
            {statusNames[status] || ''}
        </button>
        : null
}

const criteriaTexts = {
    1: '',
    2: '',
}

const customizeTextCriteria = (cellinfo) => criteriaTexts[+cellinfo.value]

const cellRenderCriteria = (cellData) => {
    const criteria = +cellData.data.criteria
    const classNames = {
        1: 'tw-bg-red-400',
        2: 'tw-bg-green-400',
    }
    return criteria === 0
        ? null
        : <div className={`tw-py-1 tw-w-20 tw-text-center tw-text-white tw-rounded-sm ${classNames[criteria]}`}>
            {criteriaTexts[criteria]}
        </div>
}

const esqTexts = {
    1: '',
    2: '',
    3: '',
}

const customizeTextEsq = (cellInfo) => esqTexts[+cellInfo.value]

const cellRenderEsq = (cellData) => {
    const esq = +cellData.data.esq
    const classNames = {
        1: 'tw-bg-red-400',
        2: 'tw-bg-green-400',
        3: 'tw-bg-green-400',
    }
    return esq === 0
        ? null
        : <div className={`tw-py-1 tw-w-20 tw-text-center tw-text-white tw-rounded-sm ${classNames[esq]}`}>
            {esqTexts[esq]}
        </div>
}

const calculateCellValueLetterOI = (rowdata) => {
    return rowdata.letterOfInterst ? '' : null
}

const customizeTextConfirmed = (cellinfo) => {
    return cellinfo.value ? '' : null
}

const calculateCellValueEvidence = (rowdata) => {
    return rowdata.evidence ? '' : null
}

const calculateCellValueEdpPlan = (rowdata) => {
    return rowdata.edpPlan ? '' : null
}

const evalutionTexts = {
    1: '',
    2: '',
}

const evalutionClassNamse = {
    1: 'tw-bg-red-400',
    2: 'tw-bg-green-400',
}

const calculateCellValue5a = (rowdata) => {
    const evalution = +rowdata.firstEvalution.value
    return evalution === 0
        ? null
        : evalutionTexts[evalution]
}

const cellRender5a = (cellData) => {
    const evalution = +cellData.data.firstEvalution.value
    return evalution === 0
        ? null
        : <div className={`tw-py-1 tw-w-20 tw-text-center tw-text-white tw-rounded-sm ${evalutionClassNamse[evalution]}`}>
            {evalutionTexts[evalution]}
        </div>
}

const calculateCellValueEvaluation5b = (rowdata) => {
    const evalution = rowdata.evaluation5b
    return typeof (evalution) === 'boolean'
        ? (evalution ? '' : '')
        : null
}

const cellRender5b = (cellData) => {
    const evalution = cellData.data.evaluation5b
    return typeof (evalution) === 'boolean'
        ? <div className={`tw-mx-auto tw-py-1 tw-w-20 tw-text-center tw-text-white tw-rounded-sm ${evalution ? 'tw-bg-green-400' : 'tw-bg-red-400'}`}>
            {evalution ? '' : ''}
        </div>
        : null
}

const calculateCellValue5c = (rowdata) => {
    const evalution = +rowdata.lastEvalution.value
    return evalution === 0
        ? null
        : evalutionTexts[evalution]
}

const cellRender5c = (cellData) => {
    const evalution = +cellData.data.lastEvalution.value
    return evalution === 0
        ? null
        : <div className={`tw-mx-auto tw-py-1 tw-w-20 tw-text-center tw-text-white tw-rounded-sm ${evalutionClassNamse[evalution]}`}>
            {evalutionTexts[evalution]}
        </div>
}

const customizeTextDuration = (cellinfo) => cellinfo.value ? `${cellinfo.value} ` : ''
